import {useRef, useState, useCallback, useEffect} from 'react';
const DURATION = 232;

const MAX_ACCEPTABLE_DRIFT = 0.2;
export const useVideoController = ({ref, playerRef}) => {
    let baseBPM = useRef();
    let lastTime = useRef(0);
    let nextTick = useRef(false);
    let lastPlaybackRate = useRef(0);
    let counter = useRef(0);
    let waiting = useRef();
    let timer = useRef(0);
    let gateTimer = useRef(undefined);
    let isRolling = useRef(false);
    let rollRate = useRef(undefined);
    let playRateUniform = useRef({value: 1.0});
    let [isPlaying, setIsPlaying] = useState(false);
    let [started, setStarted] = useState(false);

    useEffect(() => {
        window.addEventListener("message", onMessage, false);
    }, [setIsPlaying, setStarted]);

    const fastForward = useCallback(() => {
        let nextTime = Math.min(DURATION, ref.current.currentTime + 10);
        let percent = 100*nextTime / DURATION;

        playerRef.current.contentWindow.postMessage({
            type: "jacker-seek",
            body: percent
        }, "*");
        playerRef.current.currentTime = nextTime;
    }, []);

    const rewind = useCallback(() => {
        let nextTime = Math.max(0, ref.current.currentTime - 10);
        let percent = 100*nextTime / DURATION;

        playerRef.current.contentWindow.postMessage({
            type: "jacker-seek",
            body: percent
        }, "*");
        playerRef.current.currentTime = nextTime;
    }, []);

    const onMessage = useCallback((e) => {
        if (e.data.type === "is-rolling") {
            let n = parseFloat(e.data.body);
            isRolling.current = n ? true : false;
        }

        if (e.data.type === "rolling-rate") {
            let n = parseFloat(e.data.body);
            rollRate.current = n;
        }

        if (e.data.type === "jacker-playback-status") {
            setStarted(true);
            let playing = parseInt(e.data.body);
            if (playing) {
                ref.current.play();
            } else {
                ref.current.pause();
            }
            setIsPlaying(playing);
        }
        if (e.data.type === "song-player-progress") {
            let progress = parseFloat(e.data.body);
            let x = progress/100
            let sec = DURATION * x;
            let drift = sec - ref.current.currentTime;
            
            if (Math.abs(drift) > MAX_ACCEPTABLE_DRIFT || sec <= lastTime.current || sec >= lastTime.current+1.0) {
                let _drift = drift*1000;
                if (ref.current.fastSeek && isRolling.current && rollRate.current === 32) {
                    // we want to use fast seek when 1/32th rolling
                    ref.current.fastSeek(sec);
                } else {
                    ref.current.currentTime = sec;
                }
                nextTick.current = false;
            }
            lastTime.current = sec;
            if (gateTimer.current !== undefined) {
                gateTimer.current++;
                if (gateTimer.current > 1) {
                    ref.current.style.filter = "";
                    gateTimer.current = undefined;
                }
            }

        }
        if (e.data.type === "song-bpm") {
            let bpm = e.data.body;
            baseBPM.current = parseFloat(bpm);
            
        }
        if (e.data.type === "playback-bpm") {
            let bpm = e.data.body;
            if (baseBPM.current) {
                let playbackRate = bpm / baseBPM.current;
                let time = ref.current.currentTime;
                playRateUniform.current.value = playbackRate;
                ref.current.playbackRate = playbackRate;
                if (playbackRate < 1) {
                    let filter = "grayscale(" + Math.pow((1 - playbackRate)/0.55, 0.5) + ")";
                    ref.current.style.filter = filter;
                } else if (playbackRate > 1) {
                    let filter = "saturate(" + Math.pow(playbackRate,3) + ")";
                    ref.current.style.filter = filter;
                } else {
                    ref.current.style.filter = undefined;
                }
                // ref.current.fastSeek(time);
            }
        }
        if (e.data.type === "gate-strength") {
            let strength = e.data.body;
            if (strength > 0) {
                let filter = "brightness(" + (1 - strength) + ")";
                ref.current.style.filter = filter;
            } else {
                ref.current.style.filter = undefined;
            }
            gateTimer.current = 0;
        }
    }, [setIsPlaying, setStarted]);

    return {playRateUniform, isPlaying, started, rewind, fastForward};

};
