import * as THREE from "three";
import React, { useRef, useEffect, useCallback, useState} from 'react';

export const useVideoShader = ({ref, playRateUniform, videoRef, width, height, vertexShader, fragmentShader}) => {
    let renderer = useRef();
    let scene = useRef();
    let camera = useRef();
    let uniforms = useRef();
    let gateStrength = useRef(0);

    useEffect(() => {
        init();
        animate();
    }, []);

    useEffect(() => {
        window.addEventListener("message", onMessage);
    }, []);

    const onMessage = useCallback((e) => {
        if (e.data.type === "gate-strength") {
            gateStrength.current = parseFloat(e.data.body);
        }
        
    }, []);

    const init = () => {
	// Initialize the WebGL renderer
	renderer.current = new THREE.WebGLRenderer();
	renderer.current.setPixelRatio(2);
	renderer.current.setSize(width/2, height/2);
        
	// Add the renderer to the sketch container
	ref.current.appendChild(renderer.current.domElement);
        
	// Initialize the scene
	scene.current = new THREE.Scene();
        
	// Initialize the camera
	camera.current = new THREE.Camera();
        
	// Create the plane geometry
        let geometry = new THREE.PlaneGeometry(2, 2);

	// Define the shader uniforms
	uniforms.current = {
            video: {
                value: new THREE.VideoTexture( videoRef.current)
            },
            width: {
                value: width / 2.0,
            },
            height: {
                value: height / 2
            },
            gateStrength: {
                value: 0
            },
            playRate: playRateUniform.current
	};

	// Create the shader material
	var material = new THREE.ShaderMaterial({
	    uniforms : uniforms.current,
	    vertexShader,
	    fragmentShader
	});

	// Create the mesh and add it to the scene
	var mesh = new THREE.Mesh(geometry, material);
	scene.current.add(mesh);
    };

    function animate() {
	requestAnimationFrame(animate);
	render();
    }
    
    /*
     * Renders the sketch
     */
    function render() {
        if (!renderer.current.domElement) {
            return;
        }
        uniforms.current.gateStrength.value = gateStrength.current;
	renderer.current.render(scene.current, camera.current);
    }
};
