export const fragmentShader = `
uniform sampler2D video;
uniform float width;
uniform float height;
uniform float gateStrength;
uniform float playRate;

vec4 average() {
  vec4 summed = vec4(0.0,0.0,0.0,0);
  float r=0.0;
  float g=0.0;
  float b=0.0;
  vec2 uv = gl_FragCoord.xy / vec2(width*2.0, height*2.0);
  for (float i=0.0; i < 30.0; i++) {
      vec2 pt = vec2(uv.x+i/128.0, 1.0-pow(uv.y,0.5)+i/128.0);
      vec4 sampled = texture2D(video, pt);
      r += sampled.r;
      g += sampled.g;
      b += sampled.b;
  }
  vec4 c1 = -1.9*smoothstep(1.0-uv.y/32.0, sin(r/32.0), uv.x)+mix(vec4(r/32.0, g/32.0, b/32.0, 1.0), vec4(g/32.0, r/32.0, b/32.0, 1.0), r/32.0-sin(uv.x));
  vec4 c2 = mix(vec4(r/32.0, g/32.0, b/32.0, 1.0), vec4(g/32.0, r/32.0, b/32.0, 1.0), r/32.0-sin(uv.x));
return mix(c1, c2, 0.8);
}

void main() {
  vec4 avg = average();
  float grey = 0.21 * avg.r + 0.71 * avg.g + 0.07 * avg.b;
  float u_colorFactor = 0.0;
  vec4 greyScaled = vec4(avg.r * u_colorFactor + grey * (1.0 - u_colorFactor), avg.g * u_colorFactor + grey * (1.0 - u_colorFactor), avg.b * u_colorFactor + grey * (1.0 - u_colorFactor), 1.0);
  gl_FragColor = mix(greyScaled, (1.0-gateStrength*0.4)*avg, playRate);
}
`;
